import React from 'react';
import ArticleText from '../ArticleText';

class Article extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
			title: '',
			id: null
		};
	}

	render() {
		return <>
			<h3>Title</h3>
			<ArticleText />
		</>
	}
}

export default Article;
